<template>
<div class="bellUs">
  <div class="buyheader">
    <h2>{{data.label}}</h2>
  </div>
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>{{data.label}}</el-breadcrumb-item>
  </el-breadcrumb>

  <el-collapse accordion>
    <el-collapse-item v-for="item in data.children" :key="item.label">
      <template slot="title">
        {{item.label}}
      </template>
      <div v-for="item1 in item.children" :key="item1.label" class="label-1">
        <div><span>姓名：</span>{{item1.label2}}</div>
        <div><span>电话：</span>{{item1.label3}}</div>
        <div><span>职位：</span>{{item1.label4}}</div>
        <div><span>性别：</span>{{item1.label5}}</div>
        <div><span>备注：</span>{{item1.label}}</div>
      </div>
    </el-collapse-item>
  </el-collapse>
</div>
</template>

<script>
import {
  bellUs
} from '~/data'

export default {
  data() {
    return {
      data: {}
    }
  },
  created() {
    this.data = bellUs[this.$route.params.id];
    window.scrollTo(0, 0);
  }
}
</script>

<style lang="scss" scoped>
.buyheader h2 {
    line-height: 50px;
    font-size: 20px;
    text-align: left;
    padding-left: 15px;
}

.el-breadcrumb{
  padding-left:15px;
}

ol {
    padding: 0 15px;
    li {
        padding: 15px;
        margin: 8px 0;
        background-color: #f5f7fa;
        border-radius: 2px;
        color: #461b93;
    }
}

.el-collapse {
    margin: 0 15px;
}

.label-1 div:last-child{
  width:100%;
}

.label-1 div{
  float: left;
  width:50%;
  padding:  10px;
  background-color: #f5f7fa;
  span{
    color: #99a9bf;
  }
}

</style>



